import React, { useState } from 'react';
import type { HeadFC, PageProps } from 'gatsby';
import { Nav } from '../../components/Nav';

const ImageModal: React.FC = ({
  image,
  onClose,
}: {
  image: string;
  onClose: () => void;
}) => {
  return (
    <div
      id="defaultModal"
      tabIndex="-1"
      aria-hidden="true"
      className="fixed top-0 left-0 right-0 z-50 w-full p-4 overflow-x-hidden overflow-y-auto md:inset-0 h-[calc(100%-1rem)] max-h-full"
    >
      <div className="bg-white rounded-lg shadow dark:bg-gray-700">
        <div className="flex items-end p-4 border-b rounded-t dark:border-gray-600">
          <button
            onClick={onClose}
            type="button"
            className="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
          >
            <svg
              aria-hidden="true"
              className="w-5 h-5"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                clip-rule="evenodd"
              ></path>
            </svg>
            <span className="sr-only">Close modal</span>
          </button>
        </div>
        <div className="p-6 space-y-6">
          <img className="w-full" src={image} alt="image" />
        </div>
      </div>
    </div>
  );
};

const IndexPage: React.FC<PageProps> = () => {
  const [openModal, setOpenModal] = useState(false);
  const [image, setImage] = useState('');

  return (
    <Nav>
      <>
        {openModal && (
          <ImageModal image={image} onClose={() => setOpenModal(false)} />
        )}
        <div className="flex flex-col items-center justify-center w-full space-y-4">
          <div>
            <h1 className="text-4xl font-bold text-center text-green-900">
              精彩童年
            </h1>
          </div>
          <div className="flex space-x-8 justify-evenly items-center">
            <a
              onClick={() => {
                setImage('/image/Q1.jpg');
                setOpenModal(true);
              }}
            >
              <img
                src="/image/Q1.jpg"
                className="object-contain w-96 hover:opacity-50"
              />
            </a>
            <a
              onClick={() => {
                setImage('/image/Q2.jpg');
                setOpenModal(true);
              }}
            >
              <img
                src="/image/Q2.jpg"
                className="object-contain w-96 hover:opacity-50"
              />
            </a>
          </div>
        </div>
      </>
    </Nav>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
